.Tax-Game-Container {
    overflow: hidden;
    height: 100vh;
    width: 100vw;
    position: absolute;
    left: 0%;
    right: 0%;
    background-color: lightblue;
}
.desk {
    height: 70vh;
    width: 100vw;
    position: absolute;
    bottom: 0;
    background-color: #40382A;
}

.folder{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: tan;
    border-radius: 2rem;
    left: 2.5vw;
    bottom: 5vh;
    height: 80vh;
    width: 30vw;
    z-index: 2;
}

.inner-folder{
    position: relative;
    display: block;
    z-index: 10; /*above tabs*/
    border-radius: 0em;
    height: 70vh;
    width: 25vw;
    background-color: white;
    padding: 1vh 1vw;
}
.inner-folder label{
    display: block;
}

.tab-container{
    display: flex;
    position: absolute;
    margin: 0 2em;
    top: -1.5em;
    left: 0;
    gap: 1em;
}
.tab-container .tab{
    z-index: 3; /*below paper*/
    background-color: aquamarine;
    border-radius: 1em 1em 0 0;
    padding: 1em 0 2.5em 0;
    width: 3em;
    display: flex;
    justify-content: center;
    transition: transform 0.2s ease-in-out;
}
.tab-container .active{
    transform: translateY(-.5em);
}

.tab-container .disabled{
    background-color: gray;
}

form input{
    z-index: 100;
}


.file-button{
    position: absolute;
    left: 50%;
    transform: translate(0 , -50%);
}

.Paper-Container{
    width: 65%;
    height: 100%;
    position: absolute;
    right: 0;
}

.moveable-paper {
    position: absolute;
    cursor: grab;
    transition: box-shadow .1s;
}
.add-shadow {
    box-shadow: 8px 8px 1px rgba(0, 0, 0, .4);
}